import React, { useState } from 'react';
// import Game from './components/gameboard/Game';
import BoardArea from './components/gameboard/BoardArea';
import { Keyboard } from './components/keyboard/Keyboard.js';
import { WORDLIST } from './constants/wordlist.js';
import { NUM_GUESSES, WORD_LENGTH } from './constants/settings';
import './App.css';

function App() {
	const getNewWord = () => {
		const word =
			WORDLIST[Math.floor(Math.random() * WORDLIST.length)].toUpperCase();
		console.log('The secret word in Game component is: ', word);
		return word;
	};

	const [finalWord, setFinalWord] = useState(getNewWord());
  const [squares, setSquares] = useState(
    Array.from({ length: NUM_GUESSES }, (value, index) =>
      Array.from({ length: WORD_LENGTH }, (value1, index1) => ''),
    ),
  );
  const [squareColors, setSquareColors] = useState(
    Array.from({ length: NUM_GUESSES }, (value, index) =>
      Array.from({ length: WORD_LENGTH }, (value1, index1) => 'gray'),
    ),
  );

  const [currSquare, setCurrSquare] = useState([0, 0]);
  const [gameOver, setGameOver] = useState(false);
  const [won, setWon] = useState(false);
	const setNewWord = () => {
		setFinalWord(getNewWord());
	};

	return (
		<div className="App">
			<header className="App-header">
				<h2>Wordle-Plus</h2>
				<BoardArea 
        finalWord={finalWord} 
        setFinalWord={setNewWord} 
        squares={squares} 
        setSquares={setSquares}
        squareColors={squareColors}
        setSquareColors={setSquareColors}
        currSquare={currSquare}
        setCurrSquare={setCurrSquare}
        gameOver={gameOver}
        setGameOver={setGameOver}
        won={won}
        setWon={setWon}
        />
				<Keyboard />
			</header>
		</div>
	);
}

export default App;
